<template>
  <div>
    <vue-qr :logoSrc="imageUrl" :text="getcode" :size="200"></vue-qr>
  </div>
</template>
<script>
import vueQr from "vue-qr";
export default {
  name: "qecode",
  data() {
    return {
        // logo
      imageUrl: require("../../assets/logo.png"),
      text: {
        name: "",
        age: "22",
        sex: "",
      },
      code: "",
    };
  },
  components: {
    vueQr,
  },
  computed: {
    //    -- data
    getcode() {
        //  
      let code =":" + this.text.name +"," + this.text.age + "," + this.text.sex;
      this.code = code;
      return code;
    },
  },
  mounted() {},
  methods: {},
};
</script>


